body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #12c164;
  text-align: center;
}
.header {
  background-color: #007bff;
  color: white;
  padding: 10px 0;
  font-size: 1em;
  margin-bottom: 30px;
  text-shadow: 0px 3px 0px #b2a98f, 0px 14px 10px rgba(0, 0, 0, 0.15),
    0px 24px 2px rgba(0, 0, 0, 0.1), 0px 34px 30px rgba(0, 0, 0, 0.1);
  box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px,
    rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
  border-radius: 10px;
}
.app {
  max-width: 800px;
  margin: 20px auto;
  color: white;
  padding: 20px;
  background: rgb(74, 86, 50);
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
}

.search input {
  padding: 10px;
  width: calc(70% - 20px);
  margin-right: 10px;
}

.search button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.search button:hover {
  background-color: #0056b3;
}

.error {
  color: red;
  margin: 10px 0;
}

.country-info {
  margin-top: 20px;
  text-align: left;
}

.country-info .flag {
  width: 100px;
  display: block;
  margin: 0 auto 10px;
}

.country-info .gifs {
  margin-top: 20px;
}

.country-info .gif {
  width: 150px;
  margin: 5px;
}
.related-artists {
  margin-top: 20px;
}

.related-artists .artist-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.related-artists .artist {
  margin: 10px;
  text-align: center;
}

.related-artists .artist img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.footer {
  background-color: #333;
  color: white;
  padding: 10px;
  font-size: 0.9em;
  margin-top: 30px;
  box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px,
    rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
  text-shadow: 0px 4px 3px rgba(0, 0, 0, 0.4), 0px 8px 13px rgba(0, 0, 0, 0.1),
    0px 18px 23px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}
